import type { vpaidMethods } from "../types/vpaid/vpaid-methods";

interface watchForVpaidSizeI {
  container: HTMLElement;
  ASPECT_RATIO: number;
  adUnit: vpaidMethods;
}
/**
 * –í—ã—Å—Ç–∞–≤–ª—è–µ—Ç —Ä–∞–∑–º–µ—Ä –¥–ª—è VPAID –∏ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–∞–∑–º–µ—Ä–∞ —ç–∫—Ä–∞–Ω–∞
 * –≤—ã–∑—ã–≤–∞–µ—Ç resizeAd.
 *
 * –ò—Å–ø–æ–ª—å–∑—É–µ—Ç MutationObserver
 *
 * @returns –§—É–Ω–∫—Ü–∏—è –æ—Ç–ø–∏—Å–∫–∏ –æ—Ç MutationObserver
 */
export const watchForVpaidSize = (props: watchForVpaidSizeI): (() => void) => {
  const changeVPAIDHeight = () => {
    const parentWidth = props.container.offsetWidth;
    const videoHeight = parentWidth * props.ASPECT_RATIO;
    props.adUnit.resizeAd(parentWidth, videoHeight, "normal");
  };

  const observer = new MutationObserver(() => {
    requestAnimationFrame(() => {
      changeVPAIDHeight();
    });
  });
  observer.observe(props.container, { subtree: true, childList: true });
  return () => {
    observer.disconnect();
  };
};
