<script lang="ts">
  import type { VastCreativeLinear } from "vast-client";
  import { errorCodesVAST } from "../../shared/errors/error-codes-vast";
  import { TerminateError } from "../../shared/errors/terminate-error";
  import { configCreativesForVideo } from "../../shared/players/config-—Åreatives-for-video";
  import { isVisible } from "../../shared/support/is-visible";
  import type { CurrentAds } from "../../shared/types/vast/current-ads";
  import { getNextAds } from "../../shared/vast/get-next-ads";
  import IconClose from "./icon-close.svelte";
  import IconReinit from "./icon-reinit.svelte";
  import type { playerInitParamsOutstream } from "./Interfaces/player-init-params";
  import { getStyleVar } from "./player/get-styles-var";
  import { compliteStore } from "./store/complite-store";
  import { duration } from "./store/duration";
  import { errorStore } from "./store/error-store";
  import { impressionStore } from "./store/impression-store";
  import { retryStore } from "./store/retray-store";
  import { currentTime } from "./store/time-store";
  import { videoStore } from "./store/video-store";
  import { fetchVast } from "./VAST/fetch-vast";
  import Video from "./video.svelte";
  import Vpaid from "./vpaid.svelte";

  export let widthAndHeight: [number, number];

  /**
   * –ù–∞—Å—Ç—Ä–æ–π–∫–∏ –¥–ª—è –ø–ª–µ–µ—Ä–∞ –ø–æ–ª—É—á–µ–Ω–Ω—ã–µ –∏–∑ HTML –∞—Ç—Ä–∏–±—É—Ç–æ–≤
   */
  export let config: playerInitParamsOutstream;
  /**
   * –ò–Ω—Ç–µ—Ä—Ñ–µ–π—Å –¥–ª—è —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è VAST –∫–∞–º–∞–ø–∞–Ω–∏–µ–π
   */
  export let currentAds: CurrentAds;

  export let url: string;

  /**
   * –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –≤ –∫–æ—Ç–æ—Ä—ã–π –º–æ–Ω—Ç–∏—Ä—É–µ—Ç—Å—è svelte
   * –∫–æ–º–ø–æ–Ω–µ–Ω—Ç
   */
  export let container: HTMLElement;

  const creative = currentAds.ads.creatives[0] as VastCreativeLinear;
  const videoSources = configCreativesForVideo(
    creative.mediaFiles,
    config.media_querys
  );

  const isEngLang = /en/g.test(navigator.language);

  /* CSS skins */
  const styles = getStyleVar(config);
  $: cssVarStyles = Object.entries(styles)
    .map(([key, value]) => `--${key}:${value}`)
    .join(";");

  /* Flags */
  let isMuted = true;

  /* DOM REFS */

  /**
   * –î–ª—è –¥–æ—Å—Ç—É–ø–∞ –∫ DOM —ç–ª–µ–º–µ–Ω—Ç–∞–º –∏–∑ –∫–æ–¥–∞
   */
  let reinitButton = document.createElement("button");
  let wrapper = document.createElement("div");

  let isDisplayPlayer = true;
  let progressWidth = 0;

  /**
   * –ï—Å–ª–∏ –Ω–µ—Ç –¥–µ—Å–∫—Ä–∏–ø—à–µ–Ω–∞ –∏ –∏–∫–æ–Ω–∫–∏, —Ç–æ –∫—Ä–µ—Å—Ç–∏–∫ —Å–º–µ—â–∞–µ—Ç—Å—è –Ω–∞–≤–µ—Ä—Ö
   */
  let isShortHeader =
    !creative?.icons[0]?.staticResource && !currentAds.ads.description;

  currentTime.subscribe((value) => {
    progressWidth = (value / $duration) * 100 || 0;
  });

  /**
   * –î–µ–π—Å—Ç–≤–∏—è –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –ø–µ—Ä–µ–∑–∞–ø—É—Å–∫—É
   */
  const reinitAction = () => {
    currentAds.tracker.close();
    retryStore.update((value) => {
      const newValue = value + 1;
      if (newValue > parseInt(String(config.retrays))) {
        console.log(parseInt(String(config.retrays)));
        console.log(newValue);
        errorStore.set(new TerminateError(errorCodesVAST["VAST retry limit"]));
      } else {
        isDisplayPlayer = false;
        fetchVast({ url, widthAndHeight }).then((vast) => {
          currentAds = vast;
          isDisplayPlayer = true;
        });
      }
      return newValue;
    });
  };

  let isFlying = false;
  $: isFlyyingMode =
    config.fly && isFlying && !$compliteStore && $impressionStore;

  errorStore.subscribe(async (error) => {
    /**
     * Null –±—É–¥–µ—Ç –ø—Ä–∏ –æ–±–Ω—É–ª–µ–Ω–∏–∏ —Å—Ç–æ—Ä–∞, –Ω–∞ –Ω–µ–≥–æ –Ω–µ –Ω—É–∂–Ω–æ —Ä–µ–∞–∫—Ü–∏–∏
     */
    if (error === null) {
      return;
    }
    /**
     * –ï—Å–ª–∏ –ø—Ä–∏—à–µ–ª –∫–æ–¥, —Ç–æ –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –µ–≥–æ, –∏–Ω–∞—á–µ –ø–æ–ª—É—á–∏—Ç—å –∫–æ–¥ –∏–∑ –æ–ø–∏—Å–∞–Ω–∏—è
     */
    const errorCode =
      parseInt(error?.message) > 900
        ? error?.message
        : errorCodesVAST[error?.message as keyof typeof errorCodesVAST] ||
          "900";

    /**
     * –û—à–∏–±–∫–∞ —Ç—Ä–µ–∫–∞–µ—Ç—Å—è –≤ —Å–ª—É—á–∞–µ –∞–≤–∞—Ä–∏–π–Ω–æ–≥–æ –∑–∞–≤–µ—Ä—à–µ–Ω–∏—è
     * –∏ –ø–µ—Ä–µ–∑–∞–ø—É—Å–∫–∞ –ø–ª–µ–µ—Ä–∞
     */
    const trackError = () => {
      /** –¢—Ä–µ–∫–Ω—É—Ç—å –æ—à–∏–±–∫—É */
      currentAds.tracker.errorWithCode(
        errorCode,
        parseInt(errorCode) > 901 ? true : false
      );
    };

    switch (error?.toString()) {
      case `[object TerminateError]`:
        console.log("---=== TERMINATE PLAYER ===---");
        trackError();
        isDisplayPlayer = false;
        try {
          wrapper.parentNode?.removeChild(wrapper);
        } catch (error) {
          console.log(error);
        }

        break;
      case `[object ReinitError]`:
        trackError();
        reinitAction();
        break;
      case `[object AdsError]`:
        /**
         * –û—à–∏–±–∫–∞ –∫–æ—Ç–æ—Ä–∞—è –¥–æ–ª–∂–Ω–∞ –ø—Ä–∏–≤–µ—Å—Ç–∏ –∫ –ø–µ—Ä–µ—Ö–æ–¥—É –∫ —Å–ª–µ–¥—É—é—â–µ–π —Ä–µ–∫–ª–∞–º–Ω–æ–π –∫–æ–º–ø–∞–Ω–∏–∏
         * –∞ –Ω–µ –ø–µ—Ä–µ–∑–∞–ø—É—Å–∫—É –ø–ª–µ–µ—Ä–∞.
         */
        const newxtAds = await getNextAds(currentAds.vastClient);
        if (newxtAds) {
          currentAds = newxtAds;
        }
        break;
    }
  });
</script>

<svelte:window
  on:scroll="{() => {
    isFlying = !isVisible(wrapper);
  }}"
  on:resize="{() => {
    isFlying = !isVisible(wrapper);
  }}"
/>
<div
  class="df-wrapper"
  style="{cssVarStyles}; height: {widthAndHeight[1]}px;"
  bind:this="{wrapper}"
>
  {#if isDisplayPlayer}
    <!-- No header in fly mode -->
    {#if !isFlying}
      <div class="df-header" style="top:{isShortHeader ? '-35' : '-60'}px">
        <div class="df-header-ads">
          <div class="df-line"></div>
          <div class="df-header-ads-text">{isEngLang ? "Ads" : "–†–µ–∫–ª–∞–º–∞"}</div>
          <div class="df-line"></div>
          {#if isShortHeader}
            <IconClose
              onClick="{() => wrapper.parentNode?.removeChild(wrapper)}"
            />
            <div class="df-line" style="max-width: 10px"></div>
          {/if}
        </div>
        {#if !isShortHeader}
          <div class="df-header-ads-description">
            {#if creative?.icons[0]?.staticResource}
              <img
                class="df-header-ads-description-icon"
                src="{creative?.icons[0]?.staticResource}"
                alt="ads-logo"
              />
            {/if}
            {#if currentAds.ads.description}
              <div class="df-header-ads-description-text">
                {currentAds.ads.description}
              </div>
            {/if}
            <IconClose
              onClick="{() => wrapper.parentNode?.removeChild(wrapper)}"
            />
          </div>
        {/if}
      </div>
    {/if}
    <div class="{isFlyyingMode ? 'df__fly' : 'df'}">
      {#if isFlyyingMode}
        <IconClose
          onClick="{() => {
            config.fly = false;
          }}"
          floating_css="right"
        />
      {/if}
      <Video
        props="{{
          isMuted: isMuted,
          videoSources: videoSources,
          looped: String(config.looped),
          reinitButton: reinitButton,
          currentAds: currentAds,
          config: config,
        }}"
      />

      <IconReinit bind:reinitButton onClick="{reinitAction}" />

      {#if !currentAds.containVPAID}
        <div class="df--controls">
          <div class="progress-wrapper">
            <div class="df-line"></div>
            <div class="progress">
              <span class="progress-bar" style="width: {progressWidth}%"></span>
            </div>
          </div>
          <button
            class="df--controls--mute--button {isMuted
              ? 'df--controls--mute--button__mute'
              : ''}"
            aria-label="mute"
            on:click="{() => {
              isMuted = !isMuted;
            }}"
          >
            <svg
              class="df--controls--mute--svg"
              version="1.1"
              viewBox="0 0 36 36"
              width="100%"
            >
              <path
                d="M8,21 L12,21 L17,26 L17,10 L12,15 L8,15 L8,21 Z M19,14 L19,22 C20.48,21.32 21.5,19.77 21.5,18 C21.5,16.26 20.48,14.74 19,14 ZM19,11.29 C21.89,12.15 24,14.83 24,18 C24,21.17 21.89,23.85 19,24.71 L19,26.77 C23.01,25.86 26,22.28 26,18 C26,13.72 23.01,10.14 19,9.23 L19,11.29 Z"
              ></path>
              <path
                class="{isMuted ? 'df-svg-hider' : ''}"
                d="M 9.25,9 7.98,10.27 24.71,27 l 1.27,-1.27 Z"
                style="opacity: 0;"></path>
            </svg>
          </button>
          <div class="df-line" style="max-width: 8px"></div>
        </div>
      {:else}
        <Vpaid
          props="{{
            isMuted: isMuted,
            reinitButton: reinitButton,
            currentAds: currentAds,
            config: config,
            widthAndHeight: widthAndHeight,
            video: $videoStore,
            wrapper: wrapper,
            container: container,
          }}"
        />
      {/if}
    </div>
  {/if}
</div>

<style>
  .progress-wrapper {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
  }
  .progress {
    width: 100%;
    height: 6px;
    border-radius: 0px;
    overflow: hidden;
    position: absolute;
    margin: 0 !important;
  }

  .progress-bar {
    display: block;
    height: 100%;
    background: var(--color--line);
    background-size: 300% 100%;
  }
  .df-wrapper {
    margin: 80px 0 50px 0;
    position: relative;
  }
  .df__fly {
    position: fixed;
    bottom: 10px;
    right: 10px;
    z-index: 9999999;
    max-width: 300px;
  }
  .df {
    left: 0;
    width: 100%;
    height: 100%;
    grid-area: 1/-1;
  }
  .df-header {
    position: absolute;
    top: -60px;
    display: block;
    content: "Ads";
    font-size: 0.75rem;
    color: var(--color--line);
    width: 100%;
  }
  .df-line {
    background: var(--color--line);
    height: 1px;
    width: 100%;
  }
  .df-header-ads {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .df-header-ads-text {
    padding: 0 5px;
    z-index: 1;
  }
  .df-header-ads-description-text {
    font-size: 1rem;
    font-weight: bold;
    margin-right: auto;
    margin-left: 10px;
    color: var(--color--line);
  }
  .df-header-ads-description {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
    padding: 0 5px;
  }
  .df-header-ads-description-icon {
    max-width: 36px;
  }
  .df button {
    color: var(--color--line);
    background: none;
    border: none;

    cursor: pointer;
    border-radius: 50%;
    outline: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .df--controls {
    display: flex;
    list-style: none;
    width: 100%;
    justify-content: flex-end;
    box-sizing: border-box;
    align-items: center;
    position: absolute;
    bottom: -35px;
    font-size: 0.75rem;
    color: var(--color--line);
    width: 100%;
  }

  .df-svg-hider {
    opacity: 1 !important;
    transition: opacity 0.1s cubic-bezier(0.4, 0, 1, 1);
  }
  .df--controls--mute--svg {
    width: 1.8rem;
    color: var(--color--line);
    z-index: 5;
  }
</style>
