<script lang="ts">
  import { onDestroy, onMount } from "svelte";
  import type { VastCreativeLinear } from "vast-client";
  import { AdsError } from "../../shared/errors/ads-error";
  import { errorCodesVAST } from "../../shared/errors/error-codes-vast";
  import { ReinitError } from "../../shared/errors/reinit-error";
  import { log } from "../../shared/support/log";
  import type { CurrentAds } from "../../shared/types/vast/current-ads";
  import type { vpaidEvents } from "../../shared/types/vpaid/vpaid-callbacks";
  import type { vpaidMethods } from "../../shared/types/vpaid/vpaid-methods";
  import { bindToVastActions } from "../../shared/vpaid/bind-actions";
  import { checkSpec } from "../../shared/vpaid/check-spec";
  import { createFrameScript } from "../../shared/vpaid/create-frame-script";
  import { watchForVpaidSize } from "../../shared/vpaid/set-vpaid-height";
  import { ASPECT_RATIO } from "./constants";
  import type { playerInitParamsOutstream } from "./Interfaces/player-init-params";
  import { errorStore } from "./store/error-store";
  import { listenEvents } from "./VPAID/on-load/listen-events";

  interface vpaidPropsI {
    isMuted: boolean;
    currentAds: CurrentAds;
    config: playerInitParamsOutstream;
    widthAndHeight: [number, number];
    video: HTMLVideoElement;
    wrapper: HTMLDivElement;
    container: HTMLDivElement | HTMLElement;
    /** DOM ref to reinit button*/
    reinitButton: HTMLButtonElement;
  }
  export let props: vpaidPropsI = {
    isMuted: true,
    currentAds: {} as CurrentAds,
    config: {} as playerInitParamsOutstream,
    widthAndHeight: [0, 0],
    video: document.createElement("video"),
    wrapper: document.createElement("div"),
    container: document.createElement("div"),
    /** DOM ref to reinit button*/
    reinitButton: document.createElement("button"),
  };

  /* DOM REFS */
  /**
   * –î–ª—è –¥–æ—Å—Ç—É–ø–∞ –∫ DOM —ç–ª–µ–º–µ–Ω—Ç–∞–º –∏–∑ –∫–æ–¥–∞
   */
  let iframeVpaid: HTMLIFrameElement;
  let events: Partial<vpaidEvents>;
  let adUnit: vpaidMethods;
  let observerVpaidUnsub = () => {};
  /** To fix TS type error*/
  let eventName: keyof typeof events;

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ—Ç VPAID:
   * - –ü—Ä–æ–≤–µ—Ä—è–µ—Ç –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç –ª–∏ –æ–Ω –≤—Å–µ –º–µ—Ç–æ–¥—ã –∫–æ—Ç–æ—Ä—ã–µ
   * –¥–æ–ª–∂–µ–Ω –≤ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤–∏–∏ —Å–æ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–æ–º
   * - –û–±–º–µ–Ω–∏–≤–∞–µ—Ç—Å—è —Ä—É–∫–æ–ø–∞–∂–∞—Ç–∏–µ–º
   * - –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ—Ç —á–µ—Ä–µ–∑ initAd
   * - –ù–∞–≤–µ—à–∏–≤–∞–µ—Ç –¥–µ–π—Å—Ç–≤–∏—è –Ω–∞ VPAID events
   *
   * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Ñ—É–Ω–∫—Ü–∏—é –æ—Ç–ø–∏—Å–∫–∏ –æ—Ç –ø—Ä–æ—Å–ª—É—à–∏–≤–∞–Ω–∏—è —Å–æ–±—ã—Ç–∏–π VPAID
   */
  function onLoad() {
    log("VPAID LOAD, from outstream player");
    adUnit = (
      iframeVpaid.contentWindow as unknown as { getVPAIDAd: () => vpaidMethods }
    )?.getVPAIDAd();
    /**
     * –ü—Ä–æ–≤–µ—Ä–∏—Ç—å –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç –ª–∏ VPAID –≤—Å–µ –¥–µ—Ñ–æ–ª—Ç–Ω—ã–µ –º–µ—Ç–æ–¥—ã
     */
    if (!checkSpec(adUnit)) {
      errorStore.set(new AdsError());
    }

    // –û–±–º–µ–Ω –≤–µ—Ä—Å–∏—è–º–∏
    adUnit.handshakeVersion("2.0");
    const aspectRatio = props.config.aspect_ratio || ASPECT_RATIO;
    // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä–æ–≤–∞—Ç—å
    adUnit.initAd(
      props.widthAndHeight[0],
      props.widthAndHeight[0] * aspectRatio,
      "normal",
      -1,
      {
        AdParameters: (props.currentAds.ads.creatives[0] as VastCreativeLinear)
          .adParameters,
      },
      {
        slot: props.wrapper,
        videoSlot: props.video,
        videoSlotCanAutoPlay: true,
      }
    );

    observerVpaidUnsub = watchForVpaidSize({
      container: props.wrapper,
      ASPECT_RATIO: aspectRatio,
      adUnit,
    });

    events = listenEvents({
      video: props.video,
      adUnit: adUnit,
      vastTracker: props.currentAds.tracker,
      looped: props.config.looped,
      reinitButton: props.reinitButton,
    });

    for (eventName in events) {
      log("Subscribe to " + eventName);

      adUnit.subscribe(events[eventName] as () => void, eventName);
    }

    bindToVastActions(adUnit, props.currentAds.tracker);
  }
  onMount(async () => {
    log("VPAID Svelte init, from outstream player");
    const cssId = "df-vpaid";
    /* –ö–æ–Ω—Ç–µ–∫—Å—Ç —Ñ—Ä–µ–π–º–∞ */
    const frameContext = iframeVpaid.contentWindow;
    /* –î–æ–±–∞–≤–∏—Ç—å –≤–æ —Ñ—Ä–µ–π–º –ø—É—Å—Ç–æ–π –¥–æ–∫—É–º–µ–Ω—Ç —Å–æ —Å–∫—Ä–∏–ø—Ç–æ–º VPAID */
    frameContext?.document.write(createFrameScript(props.currentAds, cssId));
    /* C—Å—ã–ª–∫–∞ –Ω–∞ —Å–∫—Ä–∏–ø—Ç –≤–Ω—É—Ç—Ä–∏ —Ñ—Ä–µ–π–º–∞ */
    const script = frameContext?.document.querySelector(`script`);
    script?.addEventListener("load", onLoad);
    script?.addEventListener("error", () =>
      errorStore.set(new ReinitError(errorCodesVAST["Can't load VPAID"]))
    );
    const is_safari = navigator.userAgent.indexOf("Safari") > -1;
    if (is_safari) {
      frameContext?.document.close();
    }
  });

  onDestroy(() => {
    /*     if (iframeVpaid) iframeVpaid.removeEventListener("load", onLoad);
    if (content) content.$destroy(); */
    // When user exits the page
    log("unsubscribe from VPAID events");
    props.currentAds.tracker.close();

    for (eventName in events) {
      adUnit.unsubscribe(events[eventName] as () => void, eventName);
    }
    observerVpaidUnsub();
  });
</script>

<iframe
  sandbox="allow-same-origin allow-scripts allow-popups"
  title="vpaid"
  class="iframe"
  bind:this="{iframeVpaid}"
>
</iframe>

<style>
  .iframe {
    border: 0px;
    margin: 0px;
    opacity: 1;
    padding: 0px;
    height: 0;
    position: absolute;
    width: 0;
    top: 0;
    left: 0;
  }
</style>
