import type { VASTTracker } from "vast-client";
import { isVisible } from "../../../shared/support/is-visible";

let isImpressed = false;
/**
 * –ü—Ä–æ–≤–µ—Ä—è–µ—Ç –≤–∏–¥–∏–º–∞ –ª–∏ –¥–æ—Å—Ç–∞—Ç–∞–æ—á–Ω–∞—è —á–∞—Å—Ç—å
 * –ø–ª–µ–µ—Ä–∞ –¥–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã –∑–∞–ø—É—Å—Ç–∏—Ç—å –µ–≥–æ –∏–ª–∏ –ø–æ—Å—Ç–∞–≤–∏—Ç—å –Ω–∞ –ø–∞—É–∑—É
 */
export const autoPlayPause = (
  video: HTMLVideoElement,
  tracker: VASTTracker
) => {
  if (isVisible(video)) {
    tracker.setPaused(false);
    tracker.setDuration(1);
    video
      .play()
      .then(() => {
        if (!isImpressed) {
          tracker.trackImpression();
          isImpressed = true;
        }
        tracker.setPaused(false);
      })
      .catch((error) => {
        console.error(`–û—à–∏–±–∫–∞ —Å—Ç–∞—Ä—Ç–∞ –≤–∏–¥–µ–æ ${error}`);
      });
  } else {
    video.pause();
    tracker.setPaused(true);
  }
};
