import type { VASTTracker } from "vast-client";
import { AdsError } from "../../../../shared/errors/ads-error";
import { isVisible } from "../../../../shared/support/is-visible";
import { log } from "../../../../shared/support/log";
import type { playerInitParamsOutstream } from "../../Interfaces/player-init-params";
import { errorStore } from "../../store/error-store";
import type { vpaidEvents } from "./../../../../shared/types/vpaid/vpaid-callbacks";
import type { vpaidMethods } from "./../../../../shared/types/vpaid/vpaid-methods";

interface listenEventsI {
  video: HTMLVideoElement;
  adUnit: vpaidMethods;
  vastTracker: VASTTracker;
  looped: playerInitParamsOutstream["looped"];
  reinitButton: HTMLButtonElement;
}
/**
 * –ù–∞–≤–µ—à–∏–≤–∞–µ—Ç –Ω–∞ —Å–æ–±—ã—Ç–∏—è VPAID —Ä–∞–∑–ª–∏—á–Ω—ã–µ –¥–µ–π—Å—Ç–≤–∏—è,
 * –≤–∫–ª—é—á–∞—è —Ç—Ä–µ–∫–∏–Ω–≥ VAST urls
 */
export const listenEvents = (opt: listenEventsI): Partial<vpaidEvents> => {
  let isImpressed = false;
  /**
   * –ü–æ–∫–∞–∑—ã–≤–∞–µ—Ç–µ –∫–Ω–æ–ø–∫—É –ø–µ—Ä–µ–∑–∞–ø—É—Å–∫–∞ –∏
   * –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –≤–∏–¥–µ–æ –≤ —Å—Ç–∞—Ä—Ç–æ–≤—É—é –ø–æ–∑–∏—Ü–∏—é
   */
  const reinitAction = () => {
    opt.reinitButton.setAttribute("style", "display:block !important");
    opt.video.setAttribute("preload", "none");
    opt.video.load();
  };
  /**
   * –î–ª—è —Ç—Ä–µ–∫–∏–Ω–≥–∞ —Å–æ–±—ã—Ç–∏–π –≤ VAST
   */
  const track = opt.vastTracker;
  const events: Partial<vpaidEvents> = {
    AdLoaded: () => {
      log("Outstream AdLoaded");
      /**
       * –ü—Ä–æ–≤–µ—Ä–∏—Ç—å –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –ª–∏ –ø–ª–µ–µ—Ä –≤ –∑–æ–Ω–µ –≤–∏–¥–∏–º–æ—Å—Ç–∏ —á—Ç–æ–±—ã —Å—Ç–∞—Ä—Ç–∞–Ω—É—Ç—å VPAID
       */
      const checkVisibleInterval = setInterval(() => {
        if (isImpressed) {
          return clearInterval(checkVisibleInterval);
        }
        if (isVisible(opt.video)) {
          opt.adUnit.startAd();
          return clearInterval(checkVisibleInterval);
        }
      }, 100);
    },
    AdVolumeChange: () => {
      log("Outstream AdVolumeChange");
      opt.video.addEventListener("volumechange", (e) => {
        track.setMuted((<HTMLVideoElement>e.target).muted);
      });
    },
    AdVideoStart: () => {
      log("Outstream AdVideoStart");
      if (!isImpressed) {
        isImpressed = true;
        track.trackImpression();
      }
    },

    AdPlaying: () => {
      log("Outstream COOKIE AdPlaying");
      try {
        /**
         * –ü–æ–ø—ã—Ç–∞—Ç—å—Å—è —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∑–≤—É–∫ –≤ 0 –ø—Ä–∏ —Å—Ç–∞—Ä—Ç–µ —Ä–æ–ª–∏–∫–∞, —á—Ç–æ–±—ã
         * –æ–±–µ–∑–æ–ø–∞—Å–∏—Ç—å —Å–µ–±—è –æ—Ç –∏–Ω—Å—Ç—Ä–∏–º –∫–æ–Ω—Ç–µ–Ω—Ç–∞
         */
        opt.adUnit.setAdVolume(0);
      } catch (error) {
        console.log("----===== Set volume error  - " + error);
      }
    },
    AdError: (e: unknown) => {
      log("Outstream COOKIE AdErro");
      log(String(e), true);
      errorStore.set(new AdsError());
    },
    AdLog: (e: string) => {
      log(`Outstream COOKIE AdLog`);
      log(e);
    },
    AdImpression: () => {
      log("IMPRASSION Outstream COOKIE");
      isImpressed = true;
      track.trackImpression();
    },
    AdStopped: () => {
      log("Outstream COOKIE send STOPED");
    },
    AdVideoComplete: () => {
      log("Outstream COOKIE VIDEO COMPLETE");
      // –ï—Å–ª–∏ –∑–∞ 7 —Å–µ–∫—É–Ω–¥ —Å—Ç–æ—Ä–æ–Ω–Ω–∏–π –ø–ª–µ–µ—Ä –Ω–µ –ø–µ—Ä–µ–∑–∞–ø—É—Å—Ç–∏—Ç —Ä–µ–∫–ª–∞–º—É —Å –æ–ø—Ü–∏–µ–π looped
      // –ø–æ–∫–∞–∑–∞—Ç—å –∫–Ω–æ–ø–∫—É –ø–µ—Ä–µ–∑–∞–ø—É—Å–∫–∞
      log(`Is looped - ${opt.looped}`);

      /*  if (looped === "true") {
        loopedInterval = setTimeout(() => {
          clearTimeout(loopedInterval);
          logger.log(new ReinitError("loopedInterval is over!"));
        }, 5000);
      } else {
        videoEndActionThenNotLooped(video, autoPlayFn, reinitButton);
      } */
      track.complete();
      setTimeout(reinitAction, 175);
    },
    AdUserClose: () => {
      log("Outstream COOKIE EVENT AdUserClose");
      track.complete();
      reinitAction();
    },
    AdPaused: () => {
      log("Outstream COOKIE SEND AdPaused");
    },
    AdClickThru: (url: string) => {
      log(`AdClickThru - ${url}`);
      track.click(url);
    },
    AdVideoFirstQuartile: () => {
      log("Outstream COOKIE send AdVideoFirstQuartile");
      track.setProgress(25);
    },
    AdVideoMidpoint: () => {
      log("Outstream COOKIE send AdVideoMidpoint");
      track.setProgress(50);
    },
    AdVideoThirdQuartile: () => {
      log("Outstream COOKIE send AdVideoThirdQuartile");
      track.setProgress(75);
    },
    AdSkipped: () => {
      log("Outstream COOKIE send AdSkipped");
      track.skip();
    },
  };
  return events;
};
