interface getWitdhAndHeightI {
  aspect_ratio: number;
  parentNode?: HTMLElement;
}
/**
 * –í—ã—á–∏—Å–ª—è–µ—Ç –≤—ã—Å–æ—Ç—É –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –∞—Å–ø–µ–∫—Ç —Ä–∞—Ç–∏–æ
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç [width, height]
 */
export const getWitdhAndHeight = (
  props: getWitdhAndHeightI
): [number, number] => {
  const width = props.parentNode?.offsetWidth || 0;
  const height = Number(width * props.aspect_ratio);
  return [width, height];
};
