import type { VASTTracker } from "vast-client";
import type { vpaidMethods } from "../types/vpaid/vpaid-methods";

export const bindToVastActions = (
  adUnit: vpaidMethods,
  vastTracker: VASTTracker
) => {
  /**
   * –°—Ç–∞—Ä—Ç VPAID –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –ø—Ä–∏ —Å–∫—Ä–æ–ª–ª–µ –¥–æ –Ω–µ–≥–æ –æ—Ç
   * auto play fn
   */
  vastTracker.on("start", () => {
    console.log("--== TO VPAID start AD, starting timer on 8s");
    adUnit.startAd();
  });

  // –í–¥—Ä—É–≥ –∫—Ç–æ-—Ç–æ instream –æ—Ç–¥–∞–µ—Ç
  vastTracker.on("skip", () => {
    adUnit.skipAd();
  });
};
